@use 'core/variables' as *;
@use 'core/mixins' as *;

.footer {
	@include min-490 {
		background-image: radial-gradient(
										ellipse at 17% 15%,
										#171b1c 25%,
										#171b1c67 60%,
										transparent 100%
		),
		radial-gradient(
										circle at 40% 140%,
										#eeeeee6a 0%,
										#2b3769 25%,
										transparent
		),
		radial-gradient(
										circle at 100% 100%,
										#eeeeee4d 0%,
										#2b3769 20%,
										transparent
		),
		radial-gradient(
										ellipse at 30% 2%,
										#171b1c 70%,
										rgb(43, 55, 105) 100%,
										transparent
		),
		radial-gradient(
										circle at center left,
										rgba(238, 238, 238, 0) 10%,
										rgb(4, 5, 10) 50%,
										transparent
		);
	}

	@include screen-490 {
		background-image: radial-gradient(
										ellipse at bottom center,
										#2b37695b,
										transparent
		),
		radial-gradient(
										ellipse at 10% 19%,
										#171b1c 35%,
										#eeeeee00 70%,
										transparent 100%
		),
		radial-gradient(
										circle at 0% 100%,
										#eeeeee25 0%,
										#2b376964 30%,
										transparent
		),
		radial-gradient(circle at 100% 0%, #eeeeee41 0%, #2b3769 30%, transparent),
		radial-gradient(ellipse at 30% 2%, #171b1c 0%, #2b3769 100%, transparent);
	}
	// .footer__body
	&__body {
		position: relative;
		overflow: hidden;
		padding-block: 70px 120px;

		@include screen-1440 {
			padding-block: 50px;
		}
	}

	// .schedule__row
	&__row {
		position: relative;
		z-index: 9;
	}

	// .footer__header
	&__header {
		display: grid;
		justify-content: space-between;
		padding-bottom: 26px;
		border-bottom: 1.54px solid #bcbcbc8e;
		grid-template-columns: repeat(2, 1fr);
		gap: 60px;

		@include screen-1440 {
			gap: 20px;
		}

		@include screen-490 {
			gap: 10px;
		}

		@include screen-490 {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	// .footer__logo
	&__logo {
		display: flex;
		align-items: center;
		margin-left: -4px;

		a {
			font-size: 19px;
			line-height: 116.666667%; /* 28px/24 */
			transition: all 0.4s ease 0s;
			white-space: nowrap;
			color: #fff;

			@include screen-690 {
				font-size: 15px;
			}

			&:hover {
				opacity: 0.6;
			}
		}
	}

	// .footer__icon
	&__icons {
		display: flex;
		align-items: center;
		justify-content: flex-end;

		@include screen-490 {
			justify-content: flex-start;
		}

		.icon-telegram-fly {
			background: #039be5;
		}

		.icon-whatsapp {
			background: #4caf50;
		}

		.icon-vk-brand {
			background: $color;

			&::before {
				font-size: 1.8rem !important;
			}
		}

		.icon-youtube-lg {
			background: #f03;

			&::before {
				color: white !important;
			}
		}

		.icon-dzen {
			&::before {
				font-size: 3.299rem !important;
			}
		}

		._icon {
			width: 3.3rem;
			height: 3.3rem;
			border-radius: 50%;

			&::before {
				font-size: 2rem;
				font-weight: 400;
				color: #fff;
			}
		}

		.footer__link {
			&:hover {
				animation: shake 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) both;
			}
		}
	}

	// .footer__link
	&__link {
		white-space: nowrap;
		// line-height: 118.75%; /* 19px/16 */
		color: #fff;

		&:not(:last-child) {
			margin-right: 10px;
		}
	}

	// .footer__content
	&__content {
		position: relative;
		z-index: 9;
		display: grid;
		justify-content: space-between;
		height: 100%;
		padding-top: 25px;
		grid-template-columns:
			minmax(150px, 33.333%) minmax(170px, 180px)
			minmax(150px, 33.333%);
		gap: 60px;

		@include minmax-1440-1920 {
			gap: calc(20px + (60 - 20) * ((100vw - 1440px) / (1920 - 1440)));
		}

		@include screen-1440 {
			gap: 15px;
			column-gap: 30px;
		}

		@include screen-768 {
			grid-template-columns: repeat(2, 1fr);
		}
		@include screen-425 {
			grid-template-columns: 1fr;
		}
	}

	// .footer__column
	&__column {
		display: flex;
		flex-direction: column;
		padding-bottom: 20px;
		gap: 10px;

		&:nth-child(3) {
			@include min-768 {
				align-items: flex-end;
			}
		}

		@include screen-768 {
			&:nth-child(1) {
				border-bottom: 1px solid #414141;
			}

			&:nth-child(2) {
				grid-row: 1 / 2;
				grid-column: 1 / 3;
			}
			&:not(:last-child) {
				border-bottom: 1px solid #4141415b;
			}
		}
		@include screen-425 {
			&:nth-child(1) {
				grid-column: 1 / 2;
				grid-row: 2 / 3;
			}

			&:nth-child(2) {
				grid-column: 1 / 2;
			}
		}
	}

	//. footer__list
	&__list {
		&:not(:last-child) {
			margin-bottom: 5px;

			@include screen-690 {
				margin-bottom: 0;
			}
		}
	}

	//. footer__menu
	&__menu {
		@include screen-768 {
			display: grid;
			justify-content: space-between;
			grid-template-columns: repeat(2, 1fr);
			gap: 30px;
		}
	}

	// .footer__head
	&__head {
		font-size: 16px;
		text-align: left;
		color: #8d8d8d;

		@include screen-490 {
			font-size: 15px;
			margin-bottom: 0 !important;
		}
	}

	// .footer__info
	&__info {
		flex: 1 1 auto;

		li {
			color: #fff;

			&:not(:last-child) {
				margin-bottom: 10px;
			}

			@include screen-690 {
				font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (960 - 320)));
			}

			@include screen-490 {
				font-size: 12px;
			}
		}
	}

	// .footer__privacy-policy
	&__privacy-policy {
		line-height: 118.75%; /* 19px/16 */
		color: #fff;
	}

	// .footer__item
	&__item {
		.footer__link {
			line-height: initial;

			&:hover {
				color: #fff;
			}

			@extend .underline-fl;
		}

		&:not(:last-child) {
			margin-bottom: 5px;
		}
	}

	// .footer__contacts
	&__contacts {
		@include min-768 {
			flex: 1 1 auto;
		}

		@include screen-768 {
			margin-bottom: 2rem;
		}
	}

	// .footer__phone
	&__phone {
		font-size: 16px;
		font-weight: 400;
		line-height: 125%;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		margin-bottom: 10px;
		text-align: right;
		color: #fff;
		gap: 10px;

		span {
			white-space: nowrap;
		}

		li:not(:last-child) {
			margin-bottom: 10px;
		}

		@include screen-690 {
			font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (960 - 320)));
		}

		@include screen-490 {
			font-size: 12px;
		}
	}

	// .footer__mail
	&__mail {
		line-height: 118.75%; /* 19px/16 */
		text-align: left;
		color: #fff;
	}

	// .footer__address
	&__address {
		line-height: 150.923077%; /* 23/13 */
		max-width: 350px;
		color: #fff;

		@include min-768 {
			text-align: right;
		}
	}
}

.link-transform {
	position: relative;
	display: inline-block;
	overflow: hidden;
	height: 100%;
	transition: all 0.2s ease;

	span {
		position: relative;
		display: block;
		transform: translateY(0);

		&::before {
			font-weight: 400 !important;
			position: absolute;
			top: 100%;
			left: 0;
			content: attr(data-text);
			user-select: none;
			transition: transform 0.5s ease;
			transform: translateY(0);
			pointer-events: none;
		}

		@include minmax-1440-1920 {
			font-size: calc(14px + (16 - 14) * ((100vw - 1440px) / (1920 - 1440)));
		}
	}

	&:hover {
		span {
			animation: moveText 0.4s forwards;
		}
	}
}

// Анимация для перемещения текста
.underline-fl {
	&:hover {
		opacity: 1;

		&::before {
			width: 47%;
		}

		&::after {
			width: 47%;
		}
	}

	&::before {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 0;
		height: 1px;
		content: '';
		transition: width 0.3s ease-in-out;
		opacity: 0.8;
		background-color: #fff;
	}

	&::after {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 0;
		height: 1px;
		content: '';
		transition: width 0.3s ease-in-out;
		opacity: 0.8;
		background-color: #fff;
	}
}
